<template>
  <div class="content-wrapper">
    <div class="container-fluid">
      <!--Start Dashboard Content-->
      <div class="row">
        <div class="col-12 col-lg-12">
          <div class="card">
            <div class="card-header text-capitalize">
              registered users
            </div>
            <div class="table-responsive">
              <table class="table align-items-center table-flush table-borderless">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Phone</th>
                  <th>Registration date</th>
                </tr>
                </thead>

                <tbody>
                <tr v-for="(user) in users" :key="user.id">
                  <td>{{ user.name }}</td>
                  <td>{{ user.email }}</td>
                  <td>{{ user.phone }}</td>
                  <td>{{ user.created_at }}</td>
                </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div><!--End Row-->

      <!--End Dashboard Content-->

      <!--start overlay-->
      <div class="overlay toggle-menu"></div>
      <!--end overlay-->

    </div>
    <!-- End container-fluid-->

  </div>
</template>

<script>
export default {
  name: "UserDashboard",
  data() {
    return {
      users: [],
      next: null,
      prev: null,
      page: 1
    }
  },
  async mounted() {
    await this.getUsers(`/users?page=${this.page}`)
  },
  methods: {
    async getUsers(url) {
      this.$axios.get(url).then(response => {
        this.users = response.data.data.users
        this.next = parseInt(response.data.links.next?.toString().split('?page=', 2)[1])
        this.prev = parseInt(response.data.links.prev?.toString()?.split('?page=', 2)[1])
      })
    },
    async getNext() {
      await this.getUsers(`/users?page=${this.next}`)
    },
    async getPrev() {
      await this.getUsers(`/users?page=${this.prev}`)
    },
    async activate(id, index) {
      this.$axios.put(`/users/${id}`)
          .then(() => {
            this.users[index].status = 'active'
          })
    },
    async deactivate(id, index) {
      this.$axios.delete(`/users/${id}`)
          .then(() => {
            this.users[index].status = 'blocked'
          })
    }
  }
}
</script>

