<template>
  <div class="content-wrapper">
    <div class="container-fluid">
      <!--Start Dashboard Content-->
      <div class="card mt-3">
        <div class="card-content">
          <div class="row row-group m-0">
            <div class="col-12 col-lg-6 col-xl-3 border-light">
              <div class="card-body">
                <h5 class="text-white mb-0">{{ unique_visitors }} <span class="float-right"><i class="fa fa-user"></i></span></h5>
                <div class="progress my-3" style="height:3px;">
                  <div class="progress-bar" style="width:55%"></div>
                </div>
                <p class="mb-0 text-white small-font">Unique Visitor</p>
              </div>
            </div>
            <div class="col-12 col-lg-6 col-xl-3 border-light">
              <div class="card-body">
                <h5 class="text-white mb-0">{{ total_views }} <span class="float-right"><i class="fa fa-eye"></i></span></h5>
                <div class="progress my-3" style="height:3px;">
                  <div class="progress-bar" style="width:55%"></div>
                </div>
                <p class="mb-0 text-white small-font">Total Views </p>
              </div>
            </div>
            <div class="col-12 col-lg-6 col-xl-3 border-light">
              <div class="card-body">
                <h5 class="text-white mb-0">{{ wallet?.points ?? 0 }} <span class="float-right"><i class="fa fa-usd"></i></span></h5>
                <div class="progress my-3" style="height:3px;">
                  <div class="progress-bar" style="width:55%"></div>
                </div>
                <p class="mb-0 text-white small-font">Total Points </p>
              </div>
            </div>
            <div class="col-12 col-lg-6 col-xl-3 border-light" style="cursor: pointer" @click="copyLink">
              <div class="card-body">
                <h6 class="text-white mb-0">Link<span class="float-right"><i class="fa fa-link"></i></span></h6>
                <div class="progress my-3" style="height:3px;">
                  <div class="progress-bar" style="width:55%"></div>
                </div>
                <p class="mb-0 text-white small-font">Click to Copy Link</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-12 col-lg-8 col-xl-8">
          <div class="card" style="min-height: 460px">
            <div class="card-header">Daily registration</div>
            <div class="card-body">
              <div class="chart-container-1">
                <DailyRegistrationReport />
              </div>
            </div>
          </div>
        </div>

        <div class="col-12 col-lg-4 col-xl-4">
          <div class="card" style="min-height: 460px">
            <div class="card-header">Not yet
            </div>
            <div class="card-body">

            </div>
            <div class="table-responsive">
            </div>
          </div>
        </div>
      </div>
      <!--End Row-->
      <!--End Dashboard Content-->
      <!--start overlay-->
      <div class="overlay toggle-menu"></div>
      <!--end overlay-->
    </div>
    <!-- End container-fluid-->
  </div>
</template>

<script>
import DailyRegistrationReport from "@/components/client/DailyRegistrationReport";
export default {
  name: "UserDashboard",
  components: {DailyRegistrationReport},
  data() {
    return {
      users: [],
      next: null,
      prev: null,
      total_views: 0,
      unique_visitors: 0,
      page: 1,
      wallet: null,
    }
  },
  async mounted() {
    await this.getUsers(`/users?page=${this.page}`)
    await this.getWallet()
    await this.getViews()
  },
  methods: {
    async getUsers(url) {
      this.$axios.get(url).then(response => {
        this.users = response.data.data.users
        this.next = parseInt(response.data.links.next?.toString().split('?page=', 2)[1])
        this.prev = parseInt(response.data.links.prev?.toString()?.split('?page=', 2)[1])
      })
    },
    async getNext() {
      await this.getUsers(`/users?page=${this.next}`)
    },
    async getPrev() {
      await this.getUsers(`/users?page=${this.prev}`)
    },
    async activate(id, index) {
      this.$axios.put(`/users/${id}`)
          .then(() => {
            this.users[index].status = 'active'
          })
    },
    async deactivate(id, index) {
      this.$axios.delete(`/users/${id}`)
          .then(() => {
            this.users[index].status = 'blocked'
          })
    },
    copyLink() {
      navigator.clipboard.writeText(`${window.location.origin}/user/register?refereed_by=${this.$store.state.user.link}`);
      this.$notify({text: 'Link Copied', type: 'info'})
    },
    async getWallet() {
      this.$axios.get(`/user_wallet/${this.$store.state.user.id}`)
          .then((response) => {
            this.wallet = response.data.data
          })
    },
    async getViews() {
      this.$axios.get(`/view/${this.$store?.state?.user?.link}`)
          .then((response) => {
            this.total_views = response.data?.data?.total_views ?? 0
            this.unique_visitors = response.data?.data?.unique_visitors ?? 0
          })
    }
  },
  watch: {
    users() {
      this.getWallet()
    }
  }
}
</script>

